.header__section {
  position: fixed;
  top: 0;
  left: 50%;
  width: 100%;
  height: 120px;
  transform: translateX(-50%);
  transition: 0.3s ease;
  z-index: 99999;
}

.header__section.change-nav {
  background-color: #ffffff;
  height: 80px;
  box-shadow: 5px 0 10px rgb(214, 214, 214);
}

.header__section.change-nav .header__navbar {
  height: 80px;
}

.header__navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 120px;
  transition: 0.3s ease;
}

.header__navbar .nav-links ul {
  display: flex;
}

.header__navbar .nav-links ul li a {
  padding: 30px 16px;
  color: #192252;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  transition: 0.2s ease;
  position: relative;
}

.header__navbar .nav-links ul li a::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 0;
  transform: translateY(-100%);
  border-radius: 0 0 6px 6px;
  background-color: #424F7B;
  transition: 0.2s linear;
}

.header__navbar .nav-links ul li a:hover::after {
  height: 5px;
}

.header__navbar .header__button a {
  background-color: #0E1734;
  color: #ffffff;
  padding: 16px 14px;
  transition: 0.2s ease;
  border-radius: 4px;
  position: relative;
}

.header__navbar .header__button a::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background-color: #424F7B;
  z-index: -1;
  border-radius: 4px;
  transition: 0.2s ease;
}

.header__navbar .header__button a:hover {
  background-color: transparent;
}

.header__navbar .header__button a:hover::after {
  width: 100%;
}

.header__navbar .menuBtn .bar {
  width: 35px;
  height: 4px;
  background-color: #0E1734;
  margin: 6px 0;
}

.header__navbar .menuBtn {
  cursor: pointer;
  display: none;
}

.header__navbar .navside {
  position: absolute;
  top: 0;
  right: 0;
  width: 350px;
  max-width: 100%;
  height: 100vh;
  padding: 0 10px;
  background-color: #0E1734;
  transform: translateX(110%);
  transition: 0.3s ease;
}

.header__navbar .navside.nav-active {
  transform: translateX(0);
}

.header__navbar .navside .close_btn .bar {
  width: 30px;
  height: 4px;
  background-color: #ffffff;
}

.header__navbar .navside .close_btn {
  position: absolute;
  top: 40px;
  right: 10px;
  cursor: pointer;
  padding: 15px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header__navbar .navside .close_btn .bar1 {
  transform: rotate(-45deg) translate(11px, 11px);
}

.header__navbar .navside .close_btn .bar2 {
  transform: rotate(45deg) translate(-10px, 10px);
}

.header__navbar .navside ul {
  margin-top: 100px;
}

.header__navbar .navside ul li a {
  display: block;
  font-size: 16px;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  color: #ffffff;
  padding: 12px 10px;
  position: relative;
  transition: 0.2s linear;
}

.header__navbar .navside ul li a::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background-color: #00fff2;
  transition: 0.2s linear;
}

.header__navbar .navside ul li a:hover {
  padding-left: 15px;
}

.header__navbar .navside ul li a:hover::after {
  width: 4px;
}

.header__navbar .navside .header_button {
  margin-top: 50px;
}

.header__navbar .navside .header_button a {
  background-color: #192252;
  color: #ffffff;
  padding: 16px 14px;
  transition: 0.2s ease;
  border-radius: 4px;
  position: relative;
}

.header__navbar .navside .header_button a::after {
  content: "";
