* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  font-family: "Inter", sans-serif;
  text-decoration: none;
  list-style: none;
  outline: none;
}

::selection {
  background-color: #424F7B;
  color: #ffffff;
}

body::-webkit-scrollbar {
  width: 11px;
}

body::-webkit-scrollbar-thumb {
  background-color: #0E1734;
}

body::-webkit-scrollbar-thumb:hover {
  background-color: #424F7B;
}

.title {
  font-size: 36px;
  color: #192252;
  font-weight: 600;
  font-family: "Montserrat", sans-serif;
  line-height: 54px;
  letter-spacing: 1px;
}

.container {
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (max-width: 1124px) {
  .header__content .content__items .content__images_item .content__image:nth-child(2) {
    margin-top: 50px;
  }
}

@media screen and (max-width: 1024px) {
  .rooms__section .rooms__items .rooms__item .rooms__item_text {
    font-size: 16px;
    line-height: 24px;
  }

  .order__section .order__items .order_text__item .order__buttons {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 30px;
  }

  .footer__section .footer__items .footer__links_items {
    width: 65%;
  }
}

@media screen and (max-width: 950px) {
  .header__content .content__items .content__images_item {
    column-gap: 10px;
  }

  .order__section .order__items .order_select__items .order__select_item.arrowUp {
    height: 200px;
  }

  .order__section .order__items .order_select__items .order__select_item .select__title {
    font-size: 18px;
    font-weight: 600;
  }
}

@media screen and (max-width: 850px) {
  .header__content .content__items .content__text_item .content__title {
    font-size: 38px;
    line-height: 57px;
  }

  .header__content .content__items .content__text_item .content__text {
    font-size: 16px;
    line-height: 24px;
  }

  .cottage__section .cottage__items .cottage__text_item .title {
    font-size: 27px;
    line-height: 40px;
  }

  .cottage__section .cottage__items .cottage__text_item .cottage__text {
    font-size: 16px;
    line-height: 20px;
  }

  .rooms__section .rooms__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px 24px;
  }

  .order__section .order__items {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    row-gap: 50px;
  }

  .order__section .order__items .order_select__items {
    width: 100%;
  }
}

@media screen and (max-width: 768px) {
  .header__navbar .nav-links {
    display: none;
  }

  .header__navbar .header__button {
    display: none;
  }

  .header__navbar .menuBtn {
    display: block;
  }

  .header__content .content__items {
    display: flex;
    flex-direction: column;
    row-gap: 40px;
  }

  .header__content .content__items .content__text_item .content__title {
    font-size: 48px;
    line-height: 72px;
  }

  .header__content .content__items .content__text_item .content__text {
    font-size: 18px;
    line-height: 27px;
  }

  .header__content .content__items .content__images_item {
    column-gap: 24px;
  }

  .header__content .content__items .content__images_item .content__image:nth-child(2) {
    margin-top: 95px;
  }

  .cottage__section {
    padding-top: 100px;
  }

  .cottage__section .cottage__items .cottage__item {
    display: flex;
    align-items: flex-start;
